.compare-bikes {
  &__products {
    @include pageGrid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 48px;
      margin-bottom: 120px;
    }

    @include onDesktop {
      margin-bottom: 144px;
    }
  }

  &__product {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 2 / 6;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }
}
